@charset "UTF-8";
.ui-blockquote {
  clear: both;
}

.prismic-raw-html {
  width: 100%;
  overflow: auto;
  clear: both;
}
.prismic-raw-html table {
  overflow-x: auto;
  margin-right: -15px;
  padding-right: 15px;
  box-sizing: content-box;
  font-size: 13px;
  border-collapse: collapse;
  border-spacing: 0;
  margin-bottom: 48px;
}
.prismic-raw-html table td,
.prismic-raw-html table th {
  text-align: left;
  min-width: 120px;
  padding-right: 12px;
  padding-top: 12px;
  padding-bottom: 12px;
}
.prismic-raw-html table td:last-child,
.prismic-raw-html table th:last-child {
  padding-right: 0;
}
.prismic-raw-html table th,
.prismic-raw-html table b {
  font-weight: 600;
}
.prismic-raw-html table tbody tr td {
  border-top: 1px solid #DEE3EA;
}
.prismic-raw-html table tbody tr:first-child td {
  border-top: none;
}
.prismic-raw-html table > thead > tr > th {
  border-bottom: 1px solid #E9EDF3;
  font-weight: 600;
}

.prismic-ad {
  display: block;
  margin: var(--space-9) 0 var(--space-10);
  clear: both;
}
.prismic-ad a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: var(--space-6) 368px var(--space-6) var(--space-6);
  border: var(--border-regular);
  border-color: var(--c-indigo-30);
  border-radius: var(--radius-2);
  overflow: hidden;
  position: relative;
  min-height: 225px;
  transition: 0.2s box-shadow ease-out;
}
.prismic-ad a .ui-paragraph {
  margin-bottom: 0;
}
.prismic-ad a .prismic-ad__image {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  max-width: 337px;
  object-fit: cover;
  z-index: -1;
}
.prismic-ad a:hover {
  box-shadow: var(--elevation-3);
}
.prismic-ad .ui-heading {
  color: var(--c-indigo-100);
  margin-bottom: var(--space-2);
}
@media (max-width: 1023px) {
  .prismic-ad a {
    padding-right: var(--space-6);
  }
  .prismic-ad a .prismic-ad__image {
    opacity: 0.25;
  }
}

.prismic-youtube-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%;
  clear: both;
}

.prismic-youtube-container iframe,
.prismic-youtube-container object,
.prismic-youtube-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.prismic-floating-image {
  width: 295px;
  float: right;
  margin: 8px -20px 32px 74px;
}
.prismic-floating-image.left {
  float: left;
  margin-right: 74px;
  margin-left: -20px;
}
.prismic-floating-image img {
  box-shadow: var(--elevation-8);
}
.prismic-floating-image figcaption {
  color: var(--c-carbon-20);
  font-size: 11px;
  margin-top: 13px;
}
@media (max-width: 1023px) {
  .prismic-floating-image {
    margin-right: 0;
    margin-left: 32px;
    width: 100%;
  }
  .prismic-floating-image.left {
    margin-left: 0;
    margin-right: 32px;
  }
}

.resource-whitepaper__cta {
  background: linear-gradient(to bottom, #fff, #f6f8fc);
  border-bottom: 1px solid #e5e7eb;
  margin-top: -78px;
  padding-top: 78px;
}
.resource-whitepaper__hgroup {
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-top: var(--space-9);
  padding-bottom: var(--space-9);
  max-width: 840px;
  text-align: center;
}
.resource-whitepaper__hgroup .ui-heading-6 {
  margin-bottom: var(--space-2);
}
.resource-whitepaper__tagline {
  max-width: 800px;
  color: var(--c-carbon-60);
  letter-spacing: -0.02em;
  font-weight: 600;
  line-height: 32px;
  font-size: 18px;
}
.resource-whitepaper__content {
  padding-top: 96px;
}
.resource-whitepaper__content img {
  max-width: 100%;
  height: auto;
}
.resource-whitepaper__description p:first-child {
  color: #6e7f99;
  font-size: 18px;
  line-height: 28px;
}
.resource-whitepaper__description ul {
  list-style-type: none;
  padding: 0;
  margin: 16px 0 32px;
  line-height: 20px;
}
.resource-whitepaper__description li {
  padding-left: 20px;
  position: relative;
}
.resource-whitepaper__description li:before {
  content: "â€¢";
  margin-right: 10px;
  opacity: 0.8;
  position: absolute;
  left: 0;
}